body, html {
  height: 100%
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%
}

#humanity-title {
  font-family: sans-serif;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.2px;
  color: rgb(64, 64, 64);
  display: inline-block;
  cursor: pointer;
  margin-left: 3px;
  margin-top: 2px;
  z-index: 25;
  text-decoration: none;
}

#learn-apply {
  cursor: pointer;
  color: rgb(101, 101, 230);
  font-weight: bold;
}

button {
  cursor: pointer;
  font-family: sans-serif;
  font-size: 14px;
  font-weight: bold;
  line-height: 14px;
  letter-spacing: 0.8px;
  height: 40px;
  color: white;
  background-color: rgb(101, 101, 230);
  border-width: 0px;
  border-style: none;
  border-style: initial;
  border-color: currentColor;
  border-color: initial;
  -webkit-border-image: none;
          border-image: none;
  -webkit-border-image: initial;
          border-image: initial;
  margin: 0px;
  padding: 5px 20px;
  border-radius: 2px;

}
button:hover{
  background-color: rgb(112,112,230)
}

button:disabled{
  background-color: rgb(100,100,130)
}

.col-6 {
  overflow: hidden;
}

.set-max {
  cursor: pointer;
  color: rgb(20, 20, 20);
}
.set-max:hover {
  color: rgb(64, 64, 64);

}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media (min-width: 768px) {
  .container-small {
      width: 300px;
  }
  .container-large {
      width: 970px;
  } 
} 
@media (min-width: 992px) {
  .container-small {
      width: 500px;
  }
  .container-large {
      width: 1170px;
  } 
} 
@media (min-width: 1200px) {
  .container-small {
      width: 700px;
  }
  .container-large {
      width: 1250px;
  } 
}

.container-small, .container-large {
  max-width: 100%;
}

